.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    justify-content: space-between;
    @extend %flex-v;
    @include response(1200px) {
      padding: 0 20px;
    }
    @include response(991px) {
      justify-content: space-between;
    }
  }
  &__top {
    background: $black;
    max-height: 35px;
    height: 35px;
    transition: .3s ease-in-out;
    @extend %flex-v;
    .header__inner {
      justify-content: flex-end;
    }
    @include response(767px) {
     display: none;
    }
  }
  &__top-nav {
    ul {
      @extend %list;
      @extend %flex-v;
      li {
        font-weight: 600;
        font-size: 12px;
        line-height: 12px;
        margin-right: 30px;
        a {
          color: #fff;
          text-decoration: none;
          transition: .3s ease-in-out;
          @include hover() {
            color: $brown;
          }
        }
      }
    }
  }
  &__top-controls {
    @extend %flex-v;
  }
  &__top-control {
    font-weight: 600;
    font-size: 12px;
    &.login {
      position: relative;
      margin-right: 24px;
      &::after {
        content: '';
        position: absolute;
        right: -12px;
        top: 50%;
        transform: translateY(-50%);
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: #fff;
      }
    }
    a {
      @extend %flex-v;
      color: #fff;
      text-decoration: none;
      transition: .3s ease-in-out;
      svg {
        fill: #fff;
        margin-right: 8px;
        transition: .3s ease-in-out;
      }
      @include hover() {
        color: $brown;
        svg {
          fill: $brown;
        }
      }
    }
  }
  &__main {
    background: #fff;
    box-shadow: 0px 5px 75px rgba(0, 0, 0, 0.075);
    @extend %flex-v;
    height: 80px;
    @include response(767px) {
      height: 70px;
    }
  }
  &__menu-opener {
    display: none;
    @include response(991px) {
      display: block;
    }
  }
  &.fixed {
    .header__top {
      max-height: 0;
    }
  }
}

.main-nav {
  ul {
    @extend %list;
    @extend %flex-v;
    margin: 0 -22px;
    li {
      font-size: 14px;
      margin: 0 22px;
      a {
        color: $black;
        text-decoration: none;
        transition: .3s ease-in-out;
        @include hover() {
          color: $green;
        }
      }
    }
  }
  @include response(991px) {
    display: none;
  }
}

.logo {
  @include response(767px) {
    max-width: 135px;
  }
}

.search {
  width: 145px;
  display: flex;
  justify-content: flex-end;
  position: relative;
  @include response(991px) {
    margin: 0;
  }
  &__input {
    border: 1px solid #1BA098;
    border-radius: 5px;
    height: 40px;
    background: #fff;
    position: absolute;
    top: -5px;
    right: 0;
    z-index: 2;
    padding: 0 30px 0 25px;
    width: 0;
    visibility: hidden;
    @include transition();
  }
  &__close {
    position: absolute;
    top: 5px;
    right: 12px;
    z-index: 3;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    @include transition();
  }
  &__button {
    @extend %flex-c;
    width: 32px;
    height: 30px;
    border: 1px solid #5a5e62;
    border-radius: 5px;
    cursor: pointer;
    transition: .3s ease-in-out;
    @include hover() {
      background: $brown;
      border-color: $brown;
    }
  }
  &.active {
    .search__input {
      width: 390px;
      visibility: visible;
    }
    .search__close {
      opacity: 1;
      pointer-events: all;
    }
  }
}